import {settings} from '../../settings/config.js';

/**
 * Get template of the profile's update page for user
 * @param {Object} user
 * @return {Array} template of the profile's update page
 */
export default function template(user) {
  const gender = {
    block: 'select',
    fieldName: 'selectMale',
    options: [
      {content: '', value: ''},
      {content: '', value: 'male'},
      {content: '', value: 'female'},
      {content: '', value: 'other'},
    ],
  };
  setSelectedGender(user, gender);

  return [{
    block: 'profile-popup',
    mods: {main: true},
    content: [
      {
        block: 'title',
        wrappedInside: 'profile-popup',
        content: [' '],
      },
      {
        elem: 'content',
        content: [
          {
            elem: 'profile-icon',
            attrs: {
              src: user.img.startsWith(settings.imgPath) ?
                  user.img :
                  settings.imgPath + user.img,
            },
          },
          {
            block: 'form',
            attrs: {id: 'updateForm', novalidate: true},
            name: 'updateForm',
            mix: {'block': 'signup-form'},
            content: [
              {
                block: 'profile-info',
                wrappedInside: 'profile-popup',
                fields: [
                  {
                    name: '',
                    novalidate: true,
                    value: {
                      block: 'file-input',
                      fieldName: 'avatar',
                    },
                  },
                  {
                    name: 'Email',
                    fieldName: 'email',
                    fieldAttrs: {
                      checkable: true,
                      checkType: 'email',
                      required: true,
                      value: user.email,
                      isUpd: true,
                      type: 'email',
                      placeholder: 'your.name@site.com',
                    },
                  },
                  {
                    name: '',
                    fieldName: 'username',
                    fieldAttrs: {
                      required: true,
                      checkable: true,
                      isUpd: true,
                      value: user.username || user.nickname,
                      placeholder: 'username',
                      checkType: 'userName',
                    },
                  },
                  {
                    name: '',
                    novalidate: true,
                    value: gender,
                  },
                  {
                    name: ' ',
                    fieldName: 'password',
                    fieldAttrs: {
                      type: 'password',
                      placeholder: '',
                      isUpd: true,
                      checkable: true,
                      checkType: 'password',
                    },
                  },
                  {
                    name: ' ',
                    fieldName: 'passwordRepeat',
                    fieldAttrs: {
                      type: 'password',
                      placeholder: ' ',
                      checkable: true,
                      checkType: 'repeatPassword',
                    },
                  },
                ],
              },
              {
                block: 'profile-popup',
                elem: 'double-btn',
                elemMods: {center: true},
                content: [
                  {
                    block: 'btn',
                    attrs: {'type': 'submit'},
                    mods: {size: 'large'},
                    wrappedInside: 'profile-popup',
                    icon: 'useredit',
                    btnText: '',
                  },
                  {
                    block: 'btn',
                    mods: {size: 'large', color: 'muted'},
                    wrappedInside: 'profile-popup',
                    icon: 'delete',
                    btnText: '',
                  },
                ],
              },
            ],
          },
        ],
      },
    ],
  },
  ];
}

/**
 * Set selected gender
 * @param {Object} user
 * @param {Object} gender
 */
function setSelectedGender(user, gender) {
  if (user.gender !== '') {
    const options = gender.options;
    options.forEach((option) => {
      if (option.value === user.gender) {
        option.selected = true;
      }
    });
  }
}
