'use strict';

import View from '../core/view.js';
const bemhtml = require('../bundle.bemhtml.js').bemhtml;

const authedTemplate = [
  {
    href: 'play',
    text: '',
    type: 'gamepad',
  },
  {
    href: 'profile',
    text: '',
    type: 'user',
  },
  {
    href: 'leaders',
    text: '',
    type: 'crown',
  },
  {
    href: 'authors',
    text: '',
    type: 'users',
  },
  {
    href: 'exit',
    text: '',
    type: 'signout',
  },
  {
    href: 'chat',
    text: '',
    type: 'mail',
  },
];

const unauthedTemplate = [
  {
    href: 'single',
    text: '',
    type: 'gamepad',
  },
  {
    href: 'signin',
    text: '',
    type: 'signin',
  },
  {
    href: 'signup',
    text: '',
    type: 'key',
  },
  {
    href: 'leaders',
    text: '',
    type: 'crown',
  },
  {
    href: 'authors',
    text: '',
    type: 'users',
  },
  {
    href: 'chat',
    text: '',
    type: 'mail',
  },
];

/**
 * @class MenuView
 */
export default class MenuView extends View {
  /**
   *
   * @param {HTMLElement} parent
   */
  constructor(parent) {
    super(parent);
  }
  /**
   * create Menu page
   * @param {JSON} user data
   */
  render(user) {
    if (!user) {
      this.menuPageDrawer(unauthedTemplate);
    } else {
      this.menuPageDrawer(authedTemplate);
    }
  }

  /**
   * Get function to render menu with current data
   * @param {Array} data array of menu points
   */
  menuPageDrawer(data) {
    const template = [
      {
        block: 'menu',
        content: [
          {
            elem: 'items',
            points: data,
          },
        ],
      },
    ];
    this.parent.insertAdjacentHTML('beforeend', bemhtml.apply(template));
  }
}
